import React, { useState, useEffect } from "react";
import * as S from "./NewRecruitment.styles"; //  
import axios from "axios";
import defaultImage from "../../asset/mainLogo.png"; //   
const API_URL = process.env.REACT_APP_API_URL;

function FestivalList() {
  const [events, setEvents] = useState([]); // API   
  const [loading, setLoading] = useState(false); //   
  const [error, setError] = useState(null); //   

  useEffect(() => {
    const fetchEvents = async () => {
      setLoading(true); //  
      try {
        const response = await axios.get(
          `${API_URL}/api/home/updatedrecruitment`,
          {
            headers: {
              "Content-Type": "application/json",
              "ngrok-skip-browser-warning": "69420",
            },
          }
        );

        console.log("API Response:", response.data); // 
        setEvents(
          Array.isArray(response.data)
            ? response.data.map((event) => ({
                id: event.recruitment_id,
                title: event.recruitment_title || " ",
                description:
                  event.recruitment_description ||
                  "  .",
                image: defaultImage, //   
                endDate:
                  event.recruitment_end_date === "0000-00-00" ||
                  event.recruitment_end_date === null
                    ? " "
                    : new Date(event.recruitment_end_date).toLocaleDateString(),
              }))
            : []
        );
      } catch (err) {
        setError(err.response?.data?.message || err.message);
        setEvents([]); //      
      } finally {
        setLoading(false); //  
      }
    };

    fetchEvents();
  }, []);

  if (loading) return <S.Container>Loading...</S.Container>;
  if (error) return <S.Container>Error: {error}</S.Container>;

  return (
    <S.Container>
      <S.Title1>  {" >"}</S.Title1>
      {events.map((event) => (
        <S.EventBox key={event.id}>
          <S.ImageWrapper>
            <img
              src={event.image} //  URL   
              alt={event.title}
              style={{ width: "100%", height: "auto", borderRadius: "10px" }}
            />
          </S.ImageWrapper>
          <S.Title>{event.title}</S.Title>
          {/* <S.Description>
            {event.description.length > 30
              ? `${event.description.slice(0, 30)}...`
              : event.description}
          </S.Description> */}
          <S.Date>: {event.endDate}</S.Date>
        </S.EventBox>
      ))}
    </S.Container>
  );
}

export default FestivalList;
