import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import * as S from "./ClubApply.styles"; //   
const API_URL = process.env.REACT_APP_API_URL;
function ClubApply() {
  const { id } = useParams(); // URL recruitment_id  
  const [recruitmentInfo, setRecruitmentInfo] = useState(null); //    
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); //  

  //    
  useEffect(() => {
    const fetchRecruitment = async () => {
      try {
        const response = await axios.get(
          `${API_URL}/api/clubs/${id}/recruitment`,
          {
            headers: {
              "Content-Type": "application/json",
              "ngrok-skip-browser-warning": "69420",
            },
          }
        );

        setRecruitmentInfo(response.data); // API    
      } catch (err) {
        setError("    .");
      } finally {
        setLoading(false); //  
      }
    };

    fetchRecruitment();
  }, [id]);

  if (loading) return <S.Loading>Loading...</S.Loading>;
  if (error) return <S.Error>{error}</S.Error>;

  return (
    <S.ApplyContainer>
      {recruitmentInfo ? (
        <>
          <S.Title>{recruitmentInfo.title || "  "}</S.Title>
          <S.Description>
            {recruitmentInfo.description || "   ."}
          </S.Description>
        </>
      ) : (
        <S.Error>     .</S.Error>
      )}
    </S.ApplyContainer>
  );
}

export default ClubApply;
