import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; // useNavigate 
import * as S from "./Header.styles";
import logo from "../../asset/img.jpg";

const categories = [
  {
    title: "",
    items: [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ],
    navigateTo: "/clublist", //   
  },
  {
    title: "",
    items: [
      "",
      "",
      "",
      "",
      "",
      "",
      "ICT",
      "",
      "",
    ],
    navigateTo: "/miniclublist",
  },
  {
    title: "",
    items: ["", "", "", "", ""],
    navigateTo: "/eventinfo",
  },
  {
    title: "",
    items: [
      { name: "", navigateTo: "/introduce" },
      { name: "", navigateTo: "/notice" }, // ""     
    ],
  },
  {
    title: "",
    items: [
      { name: "", navigateTo: "/mypage" }, //   
    ],
  },
];

function Header() {
  const [activeCategory, setActiveCategory] = useState(null);
  const navigate = useNavigate(); // useNavigate  

  const handleMouseEnter = (index) => {
    setActiveCategory(index);
  };

  const handleMouseLeave = () => {
    setActiveCategory(null);
  };

  const handleCategoryClick = (category) => {
    if (category.navigateTo) {
      navigate(category.navigateTo); //   
    }
  };

  const handleItemClick = (item) => {
    if (item.navigateTo) {
      navigate(item.navigateTo); //    
    }
  };

  return (
    <S.Wrapper>
      {/*    */}
      <S.TopBar>
        <S.TopBarItem>HOME</S.TopBarItem>
        <S.TopBarItem onClick={() => navigate("/login")}>LOGIN</S.TopBarItem>
        <S.TopBarItem>PORTAL</S.TopBarItem>
        <S.TopBarItem>LANGUAGE </S.TopBarItem>
      </S.TopBar>

      {/*   */}
      <S.Container>
        <S.LogoLink to="/">
          <S.Logo src={logo} alt="Ajou Logo" />
        </S.LogoLink>
        <S.Menu>
          {categories.map((category, index) => (
            <S.MenuItem
              key={index}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              onClick={() => handleCategoryClick(category)} //   
            >
              <S.Text color={category.color}>{category.title}</S.Text>
              {activeCategory === index && (
                <S.Dropdown>
                  {category.items.map((item, idx) => (
                    <S.DropdownItem
                      key={idx}
                      onClick={() => handleItemClick(item)} //    
                    >
                      {item.name || item} {/*     */}
                    </S.DropdownItem>
                  ))}
                </S.Dropdown>
              )}
            </S.MenuItem>
          ))}
        </S.Menu>
      </S.Container>
    </S.Wrapper>
  );
}

export default Header;
