import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import * as S from "./ClubEvent.styles"; //   
const API_URL = process.env.REACT_APP_API_URL;

function ClubApply() {
  const { club_id } = useParams(); // URL event_id  
  console.log(club_id);
  const [recruitmentInfo, setRecruitmentInfo] = useState(null); //   
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); //  

  //   
  useEffect(() => {
    const fetchRecruitment = async () => {
      try {
        const response = await axios.get(
          `${API_URL}/api/clubs/${club_id}/events`,
          {
            headers: {
              "Content-Type": "application/json",
              "ngrok-skip-browser-warning": "69420",
            },
          }
        );

        console.log(response.data);
        setRecruitmentInfo(response.data); // API    
      } catch (err) {
        setError("    .");
      } finally {
        setLoading(false); //  
      }
    };

    fetchRecruitment();
  }, [club_id]);

  if (loading) return <S.Loading>Loading...</S.Loading>;
  if (error) return <S.Error>{error}</S.Error>;

  return (
    <S.ApplyContainer>
      {recruitmentInfo ? (
        <>
          <S.Title>{recruitmentInfo.title || " "}</S.Title>
          <S.Description>
            {recruitmentInfo.description || "   ."}
          </S.Description>
          <S.InfoBox>
            <S.InfoItem>
              <S.Label>:</S.Label>
              <S.Value>
                {recruitmentInfo.location || "  ."}
              </S.Value>
            </S.InfoItem>
            <S.InfoItem>
              <S.Label>:</S.Label>
              <S.Value>
                {recruitmentInfo.event_date
                  ? new Date(recruitmentInfo.event_date).toLocaleString()
                  : "  ."}
              </S.Value>
            </S.InfoItem>
            <S.InfoItem>
              <S.Label>:</S.Label>
              <S.Value>
                {recruitmentInfo.event_category || "  ."}
              </S.Value>
            </S.InfoItem>
            <S.InfoItem>
              <S.Label>:</S.Label>
              <S.Value>
                {recruitmentInfo.event_contact_phone_number ||
                  "  ."}
              </S.Value>
            </S.InfoItem>
            <S.InfoItem>
              <S.Label>:</S.Label>
              <S.Value>
                {recruitmentInfo.event_contact_email ||
                  "  ."}
              </S.Value>
            </S.InfoItem>
          </S.InfoBox>
        </>
      ) : (
        <S.Error>    .</S.Error>
      )}
    </S.ApplyContainer>
  );
}

export default ClubApply;
