import React from "react";
import { useParams } from "react-router-dom";
import * as S from "./MemberInfo.styles"; //   

function MemberInfo() {
  const { id } = useParams(); // URL id  

  //      
  const members = [
    { name: "", studentId: "20240001" },
    { name: "", studentId: "20240002" },
  ];

  return (
    <S.InfoContainer>
      <S.Title>{id}   </S.Title>
      <S.MemberList>
        {members.map((member, index) => (
          <S.MemberItem key={index}>
            <S.MemberName>{member.name}</S.MemberName>
            <S.StudentId>({member.studentId})</S.StudentId>
          </S.MemberItem>
        ))}
      </S.MemberList>
    </S.InfoContainer>
  );
}

export default MemberInfo;
