import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import * as S from "./ClubInfo.styles";
import defaultImage from "../../asset/mainLogo.png";
import ClubApply from "../ClubApply/ClubApply"; // ClubApply  
import ClubEvent from "../ClubEvent/ClubEvent";
const API_URL = process.env.REACT_APP_API_URL;

const sidebarItems = [" ", " ", " "];

function ClubInfo() {
  const { club_id } = useParams();
  const [clubInfo, setClubInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [selectedItem, setSelectedItem] = useState(" ");

  useEffect(() => {
    const fetchClubData = async () => {
      setLoading(true);
      try {
        const response = await axios.get(`${API_URL}/api/clubs/${club_id}`, {
          headers: {
            "Content-Type": "application/json",
            "ngrok-skip-browser-warning": "69420",
          },
        });
        setClubInfo(response.data);
      } catch (err) {
        console.error("API Error:", err.response || err.message);
        setError("    .");
      } finally {
        setLoading(false);
      }
    };

    fetchClubData();
  }, [club_id]);

  if (loading) return <S.Loading>Loading...</S.Loading>;
  if (error) return <S.Error>{error}</S.Error>;

  return (
    <S.PageContainer>
      <S.Sidebar>
        <S.SidebarTitle></S.SidebarTitle>
        <S.SidebarList>
          {sidebarItems.map((item, index) => (
            <S.SidebarItem
              key={index}
              $isSelected={selectedItem === item} // $isSelected 
              onClick={() => setSelectedItem(item)}
            >
              {item}
            </S.SidebarItem>
          ))}
        </S.SidebarList>
      </S.Sidebar>

      <S.InfoContainer>
        <S.Header>
          <S.ClubTitle>{clubInfo?.club_name || " "}</S.ClubTitle>
          <S.TitleBar />
        </S.Header>
        <S.CardContainer>
          <S.CardLogo
            src={clubInfo?.image || defaultImage} //   
            alt={clubInfo?.club_name || "Club Logo"}
          />
          <S.CardContent>
            <S.ClubName>{clubInfo?.club_name || " "}</S.ClubName>
            <S.CardInfoBox>
              <S.CardInfoItem>
                <S.ContactLabel></S.ContactLabel>
                <S.ContactValue>
                  {clubInfo?.club_location || "  ."}
                </S.ContactValue>
              </S.CardInfoItem>
              <S.CardInfoItem>
                <S.ContactLabel></S.ContactLabel>
                <S.ContactValue>
                  {clubInfo?.club_contact_phone_number ||
                    "  ."}
                </S.ContactValue>
              </S.CardInfoItem>
              <S.CardInfoItem>
                <S.ContactLabel>SNS</S.ContactLabel>
                <S.ContactValue>
                  <a
                    href={clubInfo?.club_sns || "#"}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {clubInfo?.club_sns || "SNS  ."}
                  </a>
                </S.ContactValue>
              </S.CardInfoItem>
            </S.CardInfoBox>
          </S.CardContent>
        </S.CardContainer>
        {/*   */}
        {selectedItem === " " && (
          <>
            <S.Section>
              <S.SectionTitle> </S.SectionTitle>
              <S.SectionContent>
                {clubInfo?.club_description || "  ."}
              </S.SectionContent>
            </S.Section>

            <S.Section>
              <S.SectionTitle></S.SectionTitle>
              <S.SectionContent>
                {clubInfo?.club_category || "  ."}
              </S.SectionContent>
            </S.Section>

            <S.Section>
              <S.SectionTitle> </S.SectionTitle>
              <S.SectionContent>
                {clubInfo?.detail_category_1 || "   ."}
              </S.SectionContent>
            </S.Section>

            <S.Section>
              <S.SectionTitle>  </S.SectionTitle>
              <S.SectionContent>
                {clubInfo?.college_name || "  ."} /{" "}
                {clubInfo?.department_name || "  ."}
              </S.SectionContent>
            </S.Section>
          </>
        )}
        {selectedItem === " " && <ClubApply />} {/*    */}
        {selectedItem === " " && <ClubEvent />}
      </S.InfoContainer>
    </S.PageContainer>
  );
}

export default ClubInfo;
