import styled from "styled-components";

export const CategoryContainer = styled.div`
  display: flex;
  flex-direction: column; /*     */
  gap: 10px;
  padding: 0 40px;
  justify-content: center;
  margin-top: 30px;
`;

export const Title = styled.h1`
  width: 100%;
  font-size: 14px;
  font-weight: 700;
  color: #1d1d1f;
  text-align: left;
  margin-bottom: 20px;
  margin-left: 10px;
`;

export const ScrollWrapper = styled.div`
  display: flex;
  gap: 10px; /*    */
  width: 100%; /*   */
  padding: 10px 0;
  justify-content: space-between; /*    */
  overflow-x: auto; /*    */
  scroll-behavior: smooth;

  /*   */
  &::-webkit-scrollbar {
    display: none;
  }
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
`;

export const CategoryBox = styled.div`
  flex: 0 0 auto; /*      */
  width: 150px;
  height: 150px;
  border-radius: 16px;
  background-color: #ffffff;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.05);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.7); /*    */
  }

  span {
    position: absolute;
    font-size: 14px;
    font-weight: 700;
    color: white;
    z-index: 2;
    text-align: center;
  }
`;
