import React, { useState, useEffect } from "react";
import * as S from "./NoticePage.styles"; //  
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL;

function NoticePage() {
  const [activeTab, setActiveTab] = useState(""); //   
  const [notices, setNotices] = useState([]); //   
  const [loading, setLoading] = useState(false); //  
  const [error, setError] = useState(null); //  

  useEffect(() => {
    const fetchNotices = async () => {
      setLoading(true);
      try {
        const endpoint =
          activeTab === ""
            ? `${API_URL}/api/home/notice`
            : `${API_URL}/api/home/partners`; //    API 
        const response = await axios.get(endpoint, {
          headers: {
            "Content-Type": "application/json",
            "ngrok-skip-browser-warning": "69420",
          },
        });
        //    (ID )
        const sortedData = response.data.sort((a, b) => b.id - a.id);
        setNotices(sortedData); //    
      } catch (err) {
        setError("    .");
      } finally {
        setLoading(false); //  
      }
    };

    fetchNotices();
  }, [activeTab]); //    API 

  if (loading) return <S.Loading>Loading...</S.Loading>;
  if (error) return <S.Error>{error}</S.Error>;

  return (
    <S.Container>
      {/*   */}
      <S.Title>{activeTab}</S.Title>
      <S.TitleBar />

      {/*   */}
      <S.TabContainer>
        <S.TabItem
          active={activeTab === ""}
          onClick={() => setActiveTab("")}
        >
          
        </S.TabItem>
        <S.TabItem
          active={activeTab === ""}
          onClick={() => setActiveTab("")}
        >
           
        </S.TabItem>
      </S.TabContainer>

      {/*   */}
      <S.List>
        <S.ListHeader>
          <S.ListColumn></S.ListColumn>
          <S.ListColumn></S.ListColumn>
          <S.ListColumn></S.ListColumn>
        </S.ListHeader>
        {notices.map((notice) => (
          <S.ListRow key={notice.id}>
            <S.ListColumn>{notice.id}</S.ListColumn>
            <S.ListColumn>
              <a href={notice.link} target="_blank" rel="noopener noreferrer">
                {notice.title}
              </a>
            </S.ListColumn>
            <S.ListColumn>{notice.date}</S.ListColumn>
          </S.ListRow>
        ))}
      </S.List>
    </S.Container>
  );
}

export default NoticePage;
