import React, { useState } from "react";
import * as S from "./AcademicSchedule.styles";

//   
const scheduleData = {
  1: [
    { title: " ", date: "01.01 ()" },
    {
      title: " 4  1  ",
      date: "01.09 () ~ 01.18 ()",
    },
  ],
  2: [
    {
      title: " 4  2  ",
      date: "01.27() ~ 02.03 ()",
    },
    { title: "1 ", date: "02.19 () ~ 02.25 ()" },
    { title: " ", date: "02.21 ()" },
    { title: "", date: "02.25 ()" },
  ],
  //     
};

function AcademicSchedule() {
  const [currentMonth, setCurrentMonth] = useState(2); //   

  //   
  const handlePrevMonth = () => {
    setCurrentMonth((prev) => (prev === 1 ? 12 : prev - 1));
  };

  //   
  const handleNextMonth = () => {
    setCurrentMonth((prev) => (prev === 12 ? 1 : prev + 1));
  };

  return (
    <S.ScheduleContainer>
      {/*     */}
      <S.Header>
        <S.ArrowButton onClick={handlePrevMonth}></S.ArrowButton>
        <S.CurrentMonth>{currentMonth}</S.CurrentMonth>
        <S.ArrowButton onClick={handleNextMonth}></S.ArrowButton>
      </S.Header>

      {/*   */}
      <S.ScheduleList>
        {scheduleData[currentMonth]?.length > 0 ? (
          scheduleData[currentMonth].map((item, index) => (
            <S.ScheduleItem key={index}>
              <S.Title>{item.title}</S.Title>
              <S.Date>{item.date}</S.Date>
            </S.ScheduleItem>
          ))
        ) : (
          <S.NoSchedule>  .</S.NoSchedule>
        )}
      </S.ScheduleList>
    </S.ScheduleContainer>
  );
}

export default AcademicSchedule;
